// colors
$black: #000;
$grey-dark: #8c989e;
$grey: #cbd0d3;
$grey-light: #e7eaeb;
$white: #fff;

$red: #dc4e5f;
$red-light: #e67b89;

$blue: #2ba6cb;
$blue-light: #73c9e1;
$blue-lighter: #c9e1ff;

$yellow-dark: #c57a01;
$yellow: #fbad2f;

$green-dark: #3ac162;
$green: #86ab0b;
$green-lighter: #d3ffb8;

// sizes
$padding: 10px;
$resolution: 16;

// mixins
@mixin eight-bit {
  image-rendering: crisp-edges;
  image-rendering: pixelated;
}
